var nameMap = {
  ie: t('setting', 'Internet Explorer'),
  edge: t('setting', 'Edge'),
  firefox: t('setting', 'Firefox'),
  chrome: t('setting', 'Google Chrome'),
  safari: t('setting', 'Safari'),
